import React from 'react';

import Image2 from './images/2.png';
import Image3 from './images/3.png';
import Image4 from './images/4.png';
import Image5 from './images/5.png';

import {Footnotes, Footnote, Ref} from '../util.js';

export default function() {
    return <>
        <p>Rotate the diagram \(60 \degree\) counterclockwise about \(A\):</p>
        <img src={Image2} alt='Diagram after rotating about A.'/>
        <p>At this point, we see that the triangle \(PCP'\) has sidelengths \(a\), \(b\), and \(c\), so these sidelengths do indeed form a triangle as claimed.
            Next, rotate the original diagram \(60\degree\) counterclockwise about \(C\):
        </p>
        <img src={Image3} alt='Diagram after rotating about C.'/>
        <p>And finally, about \(B\):</p>
        <img src={Image4} alt='Diagram after rotating about B.'/>
        <p>At this point, we have a large equilateral triangle with sidelength \(2\ell\), partitioned into twelve smaller triangles:<Ref id={1}/></p>
        <ol>
            <li>three triangles with sidelengths \(a, b, c\),</li>
            <li>two triangles with sidelengths \(a, b, \ell\),</li>
            <li>two triangles with sidelengths \(a, c, \ell\),</li>
            <li>two triangles with sidelengths \(b, c, \ell\),</li>
            <li>and three equilateral triangles, with sidelengths \(a, b, c\) respectively.</li>
        </ol>
        <p>The triangles of type 2, 3, and 4 fit together neatly to form two equilateral triangles of sidelength \(\ell\). We therefore have the following decomposition:</p>
        <img src={Image5} alt='Decomposition of large triangle.'/>
        <p>In particular, the area of the triangle we’re looking for is entirely determined by the combined area of the three equilateral triangles with sidelengths \(a\), \(b\), and \(c\). This combined area is proportional to \(a^2 + b^2 + c^2\). Treating all points as vectors in {String.raw`\(\mathbb{R}^2\)`}, we obtain</p>
        <p>{String.raw`\[\begin{aligned}
        &a^2 + b^2 + c^2 \\
        &= (A-P)^2 + (B-P)^2 + (C - P)^2 \\
            &= A^2 + B^2 + C^2 + 3P^2 - 2 \, (A + B + C) \cdot P \\
            &= (A^2 + B^2 + C^2 - 3 O^2) + 3 \, (P - O)^2,
        \end{aligned}\]`}
        which is entirely determined by the length \(OP\), as desired.
            <span className='qed'/>
        </p>

        <Footnotes>
            <Footnote id={1}>Strictly speaking, the decomposition works as shown if the original point lies within the triangle \(ABC\). If not, then the proof still works using signed areas (but is a bit harder to follow).</Footnote>
        </Footnotes>
    </>;
}
